    //virkailija.opintopolku.fi/koodisto-service/rest/json/julkaisufoorumitaso/koodi?onlyValidKoodis=false    
    ,julkaisufoorumitaso: [{"arvo":"1","selite":{"FI":"1=Perustaso","SV":"1=Basniv","EN":"1=Basic Level"},"kuvaus":{"FI":"Tasoluokkaan 1 kuuluu tieteellisten tutkimustulosten julkaisemiseen erikoistuneita vertaisarvioituja julkaisukanavia, joilla on tieteenalan asiantuntijoista koostuva toimituskunta.","SV":"I niv 1 ingr kollegialt granskade publiceringskanaler som r specialiserade p publicering av vetenskapliga forskningresultat, som har en redaktionskommitt bestende av vetenskapsgrenens sakkunniga.","EN":"Level 1 includes peer-reviewed publication channels that are specialised in the publication of scientific research outcomes and have an editorial board of experts of the discipline."}},{"arvo":"0","selite":{"FI":"0=Ei tyt kriteerej","SV":"0=Uppnr inte kriterierna","EN":"0=Does not meet the criteria"},"kuvaus":{"FI":"Tasolle 0 sijoitetaan ne julkaisukanavat, jotka eivt tyt jotakin tason 1 kriteereist.","SV":"Till niv 0 frs de publiceringskanaler som inte uppfyller ngot av kriterierna fr niv 1.","EN":"Level 0 includes all publication channels that do not meet one or several of the Level 1 criteria."}},{"arvo":"-","selite":{"FI":"Ei arvioitu","SV":"Inte bedmned","EN":"Not evaluated"},"kuvaus":{"FI":"Jos tasoluokan kohdalla ei ole minknlaista merkint, julkaisukanavaa ei ole arvioitu.","SV":"Om det inte finns ngon markering vid nivn, r publiceringskanalen fr nrvarande inte bedmned.","EN":"If there is no marking, the publication channel in question is not evaluated."}},{"arvo":"2","selite":{"FI":"2=Johtava taso","SV":"2=Ledande niv","EN":"2=Leading Level"},"kuvaus":{"FI":"Tasolle 2 voidaan luokitella eri tieteenalojen johtavia tieteellisi julkaisukanavia, joissa tutkijat eri maista julkaisevat parhainta tutkimustaan. Psntisesti nm ovat kansainvlisi tieteellisi julkaisukanavia, joiden toimitus-, kirjoittaja- ja lukijakunta edustavat eri kansallisuuksia. Ihmistieteiss voidaan tasolle 2 luokitella mys alansa johtavia suomen- tai ruotsinkielisi julkaisukanavia. Niden tulee olla julkaisukanavia, jotka kattavat oman tieteenalansa suomalaisen yhteiskunnan, kulttuurin ja historian erityispiirteit ksittelev tutkimusta mahdollisimman laajasti, ja joissa julkaisemista pidetn yht trken meriittin kuin julkaisemista tasolle 2 luokitelluissa vieraskielisiss julkaisukanavissa.","SV":"Till niv 2 kan fras de inom de olika vetenskapsomrdena ledande vetenskapliga publiceringskanalerna, i vilka forskare frn olika lnder publicerar sin bsta forskning. I regel r dessa internationella vetenskapliga publiceringskanaler vars redaktion, skribenter och lsekrets representerar olika nationaliteter. Inom humanvetenskaper kan till niv 2 fras ocks de inom sitt omrde ledande finsk- eller svensksprkiga publiceringskanaler. Dessa br vara publiceringskanaler som tcker forskning kring det finlndska samhllets, den finlndska kulturens och historiens srdrag inom respektive vetenskapsgren mjligast vida, och i vilka publicering anses en lika viktig merit som publicering i publiceringskanaler p frmmande sprk som frts till niv 2.","EN":"Level 2 can be awarded to leading academic publication channels of various disciplines, with the researchers from various countries publishing their best research outcomes in them. These are mainly international publications channels, with the editors, authors and readers representing various nationalities. In Humanities and Social Sciences, Level 2 can also include leading Finnish or Swedish-language publication channels covering research which specialises in the aspects of the Finnish society, culture or history in their particular field as widely as possible. Publishing in these channels is seen to be as important a merit as publishing in an international Level 2 channel."}},{"arvo":"3","selite":{"FI":"3=Korkein taso","SV":"3=Hgsta niv","EN":"3=Top Level"},"kuvaus":{"FI":"Tasolle 3 voidaan luokitella eri alojen korkeatasoisimmat tieteelliset julkaisukanavat, joille seuraavat kriteerit tyttyvt. Ensinnkin niiden tutkimus edustaa alansa korkeinta tasoa ja sen vaikuttavuus on erittin suuri. Lisksi ne kattavat omaa tieteenalaansa laajasti, eivtk rajoitu kapean erikoistematiikan ksittelyyn. Lopuksi niiden kirjoittaja- ja lukijakunta on kansainvlist ja toimituskunnissa toimivat alan johtavat tutkijat.","SV":"Till niv 3 kan fras de olika omrdenas mest hgklassiga vetenskapliga publikationsserier. Deras forskning representerar den hgsta nivn inom sitt omrde och dess genomslagskraft r mycket stor. De omfattar det egna vetenskapsomrdet vitt, och begrnsar sig inte till behandling av en snv specialtematik. Deras skribent- och lsekrets r internationell och redaktionskommitter bestr av omrdets ledande forskare.","EN":"Level 3 can include the supreme-level publication channels of various disciplines meeting the following requirements. First, the research published in them represents the highest level in the discipline and has extremely consistent impact. Second, the channels cover the discipline comprehensively, and is not limited to the discussion of narrow special themes. Finally, the channels have international authors and readers and the editorial boards are constituted by the leading researcher in the field."}}]
